module.exports = () => (contentElement, titleElement, onDismiss) => {

    const $modal = document.createElement('div');
    $modal.className = 'modal hide';

    if (contentElement.className) {
        $modal.classList.add(`${contentElement.className}-modal`)
    }

    const $dismiss = document.createElement('span');
    $dismiss.className = 'dismiss';
    $dismiss.addEventListener('click', onDismiss);

    const $title = document.createElement('div');
    $title.className = 'modal-title';
    $title.append(titleElement || (contentElement.title || ''));
    $title.append($dismiss);

    const $content = document.createElement('div');
    $content.className = 'modal-content';
    $content.append(contentElement);

    const $window = document.createElement('div');
    $window.className = 'modal-window';
    $window.append($title, $content);

    $modal.append($window);

    /*
    Dismiss when the modal background is clicked.
    Stop propagration of the modal window to prevent dismissal when clicked.
    */
    $modal.addEventListener('click', onDismiss);
    $window.addEventListener('click', e => e.stopPropagation());

    return $modal;

};
