module.exports = ({ gravatar, stores, services }) => () => {

    const { insertTag } = services.tags;
    const { freetext, fallback } = stores.gravatar.state;
    const emails = freetext.split(/[\r\n,;]+/g).map(s => s.trim()).filter(s => s);

    gravatar.fetchBatch(emails, fallback, insertTag);

    stores.gravatar.setState({ freetext: '' });

};
