require('babel-polyfill');

const wire = require('./wire');
const constants = require('./constants');

const modules = {
    gravatar: require('./gravatar'),
    lib: require('./lib'),
    stores: require('./stores'),
    services: require('./services'),
    styles: require('./styles'),
    components: require('./components'),
    views: require('./views'),
    start: require('./start')
};

module.exports = (args = {}) => {
    const { fetch = window.fetch, random = Math.random } = args;

    const wiring = {
        gravatar: ({ constants }) => ({ fetch, ...constants.gravatar }),
        lib: 'random',
        stores: 'lib',
        services: 'constants lib gravatar stores',
        styles: 'lib stores services',
        components: 'lib',
        views: 'constants lib stores services components',
        start: 'constants stores services styles views'
    };

    const app = wire(modules, wiring, { random, constants });

    app.services.state.initialise();

    return app;

};
