module.exports = ({ stores }) => () => {

    const $gravatar = document.createElement('img');
    $gravatar.className = 'gravatar';
    $gravatar.src = 'img/gravatar-icon.svg';
    $gravatar.title = 'Import images from Gravatar';

    $gravatar.addEventListener('click', () => {
        stores.app.setState({ modal: 'gravatar' });
    });

    return $gravatar;

};
