module.exports = ({ services, components, views }) => () => {

    const $container = components.dropzone(services.tags.insertImage());
    $container.className = 'dropzone';

    const $header = document.createElement('header');

    $header.append(
        views.header.titleBar(),
        views.header.navBar()
    );

    const $controlPanel = document.createElement('div');
    $controlPanel.className = 'control-panel';

    $controlPanel.append(
        views.roles.roleList(),
        views.intro(),
        views.notices.briefUsageTip(),
        views.options.optionsBar(),
        views.notices.multipleAvatars()
    );

    $container.append(
        $header,
        $controlPanel,
        views.tags.tagList()
    );

    return $container;

};
