module.exports = ({ fetch, gravatar, domain, size }) => async (email, fallback) => {

    const emailHash = gravatar.hashEmail(email);
    const url = `${domain}/avatar/${emailHash}?r=g&s=${size}&d=${fallback}`;
    const response = await fetch(url);
    const data = await response.blob();
    return URL.createObjectURL(data);

};
