module.exports = ({ stores }) => roleId => {

    const { onChange, getState } = stores.roles.find(roleId);

    const $style = document.createElement('style');

    const colourChanged = () => {
        const { colour } = getState(roleId);

        $style.textContent = `
            .role${roleId} .tag-image { 
                border-color: ${colour}; 
            }
            
            .role${roleId} .role-badge { 
                background-color: ${colour}; 
            }`;
    }

    onChange({ colour: colourChanged });

    return $style;

};
