module.exports = ({ lib, components }) => () => {

    const $tip = document.createElement('div');
    $tip.innerHTML = `
        All names and roles are editable. New roles can be added by editing the role on each tag.<br>
        Before dragging images, save time by renaming files to
        <em>name[+role].ext</em>, e.g. matt.jpg or matt+dev.jpg`;

    const $notice = components.notice($tip);
    const noticeVisibility = lib.visibilityToggle($notice);
    noticeVisibility.show();
    return $notice;

};
