module.exports = ({ lib, stores, services }) => roleData => {

    const changes = {
        name: roleData.name.trim().toUpperCase(),
        colour: roleData.colour || lib.randomColour().hex
    };

    const roleId = stores.roles.insert({ ...roleData, ...changes }, roleId => {
        const { onChange, state: role } = stores.roles.find(roleId);

        const wireEvents = () => onChange({ name: services.roles._propagateRoleName(roleId) });
        const cacheNilRole = () => stores.app.setState({ nilRoleId: roleId });

        role.name ? wireEvents() : cacheNilRole();
    });

    const { state: role } = stores.roles.find(roleId);
    return role;

};
