module.exports = ({ stores, views, container }) => () => {

    const $intro = document.createElement('div');
    $intro.innerHTML = `        
        <strong>Drag & drop</strong> images and/or 
        <strong><a>import from Gravatar</a></strong>.
        Print, trim, laminate, and attach to your agile board.`;

    const $gravatar = $intro.querySelector('a');

    $gravatar.addEventListener('click', () => {
        stores.app.setState({ modal: 'gravatar' });
    });

    return $intro;

};
