module.exports = () => (callback, milliseconds) => {

    let timeoutId;

    return () => {
        if (timeoutId) clearTimeout(timeoutId);
        if (milliseconds === 0) return callback();
        timeoutId = setTimeout(() => { callback(); timeoutId = null; }, milliseconds);
    };
};



/*

- Delays a request to invoke `callback` until no further requests are made for `milliseconds`.
- Serves as an optimisation to prevent unnecessary invocations when requests made in quick succession.

*/
