module.exports = ({ constants, lib, stores, services }) => {

    const adjustInstanceCounts = () => {
        const { _planInstanceCountAdjustment, _insertInstance, _removeInstance } = services.tags;

        const tags = stores.tags.items;
        const modeCounts = lib.pick(stores.options.getState(), constants.options.modes);
        const plans = _planInstanceCountAdjustment(tags, modeCounts);

        plans.forEach(plan => {
            plan = { insert: [], remove: [], ...plan };
            plan.insert.forEach(_insertInstance);
            plan.remove.forEach(_removeInstance);
        });
    };

    return lib.debounce(adjustInstanceCounts, constants.debounce.adjustInstanceCounts);

};
